import React from 'react';
import { connect } from 'react-redux';
import { addTodo } from '../actions';

const AddTodo = ({ dispatch }) => {
  let input;
  return (
    <div>
      <form onSubmit={
        (event) => {
          // React „Åß„ÅØ false „ÇíËøî„Åó„Å¶„ÇÇ„Éá„Éï„Ç©„É´„Éà„ÅÆÂãï‰Ωú„ÇíÊäëÊ≠¢„Åô„Çã„Åì„Å®„Åå„Åß„Åç„Åæ„Åõ„Çì„ÄÇÊòéÁ§∫ÁöÑ„Å´ preventDefault „ÇíÂëº„Å≥Âá∫„ÅôÂøÖË¶Å„Åå„ÅÇ„Çä„Åæ„Åô„ÄÇ
          // from https://ja.reactjs.org/docs/handling-events.html
          event.preventDefault();
          const text = input.value.trim();
          input.value = '';
          // „ÉÜ„Ç≠„Çπ„Éà„Å™„Åó„É™„Çπ„Éà„Çí‰Ωú„Çâ„Å™„ÅÑ
          if(!text){
            return;
          }
          dispatch(addTodo(text)); // (ÂÖ®„Å¶„ÅÆreducer„ÅåÂëº„Å≥Âá∫„Åï„Çå„Çã)
        }
      }>
        <input ref={(element) => input = element} />
        <button type="submit">
          Add Todo
        </button>
      </form>
    </div>
  );
};

export default connect()(AddTodo);
