import React from 'react';
import { connect } from 'react-redux';
import { setVisibilityFilter } from '../actions';

const Link = ({ active, children, onClick }) => (
  <button
    onClick={onClick}
    disabled={active}
    style={{
      marginLeft: '4px',
    }}
  >
    {children}
  </button>
);

// state „ÅØ„ÄÄredux„ÅßÁÆ°ÁêÜ„Åó„Å¶„ÅÑ„ÇãÂÖ®„Å¶„ÅÆstate„ÇíÊåÅ„Å£„Å¶„ÅÑ„Çã
// ownProps„ÅØconnect„Åó„ÅücomponentÊú¨Êù•„ÅÆprops„ÇíÂèñÂæó„Åô„Çã
const mapStateToProps = (state, ownProps) => ({
	active: ownProps.filter === state.visibilityFilter
});

const mapDispatchToProps = (dispatch, ownProps) => ({
	onClick: () => dispatch(setVisibilityFilter(ownProps.filter))
});

// Á¨¨‰∏ÄÂºïÊï∞„ÅØcomponent„Å´Ê∏°„Åôprops„ÇíÂà∂Âæ°„Åô„Çã
// Á¨¨‰∫åÂºïÊï∞„ÅØreducer„ÇíÂëº„Å≥Âá∫„Åó„Å¶„ÄÅredux„ÅßÁÆ°ÁêÜ„Åó„Å¶„ÅÑ„Çãstate„ÇíÊõ¥Êñ∞„Åô„Çã
// ()ÂÜÖ„ÅØÂèñÂæó„Åó„Åü„Éá„Éº„Çø„Çíprops„Å®„Åó„Å¶Êâ±„ÅÑ„Åü„ÅÑcomponent„ÇíÊåáÂÆö„Åô„Çã
export default connect(
	mapStateToProps,
	mapDispatchToProps
)(Link);
